@use 'settings/variables' as v;
@use 'tools/mixins' as m;

.c-submenus {
    &__cuenta, &__ayuda {
        @include m.position (absolute, 2rem, 0, null, null);
    } 
    &__cesta {
        @include m.position (absolute, 2.9004rem, 0, null, null);
    }
    &__contenedor {
        min-width: 250px;
        background-color: map-get(v.$colores, color-secundario);
        color: map-get(v.$colores, color-primario);
        padding: 0.5rem;
        border: 3px solid map-get(v.$colores, color-borde-cesta);
        border-radius: 3px 0 3px 3px;
        h4 {
            font-weight: bold;
            padding: 0.3rem 0 0.8rem 0;
        }
    }
    &__secciones {
        @include m.flex (column, nowrap, center, center);
        border-bottom: 1px dotted #000;
        padding: .5rem;
        button {
            margin-bottom: 0.5rem;
            padding: 0.625rem;
        }
        & > i {
            margin-right: 0.5rem;
            color: map-get(v.$colores, color-fondo-hover);
        }
        span {
            font-weight: bold;
            font-size: 1.2em
        }
        &--mod {
            @include m.flex (row, nowrap, center, center);    
        }
        &--mod3 {
            @include m.flex (row, nowrap, space-around, center);            
        }
        &--mod2 {
            @include m.flex (column, nowrap, center, flex-start); 
            p:hover {
                cursor: pointer;
                text-decoration: underline;
            }   
        }
    }
    &__secciones:last-child {
        border-bottom: none;
    }
    &__img {
        max-width: 30%;
    }
    &__contenido {
        max-width: 70%;
    }
    &__eliminar {
        color: blue;
        border-left: 1px solid #000;
        margin-left: 0.4rem;
        padding-left: 0.4rem;
        &:hover {
            text-decoration: underline;
        }
    }
    &__separador {
        border-top: 1px solid #000;
        width: 100%;
        background-color: #f1f1f1;
        @include m.flex (row, nowrap, flex-end, center);
        padding: 0.2rem 1rem 0.2rem 0.2rem;
        text-transform: uppercase;
        font-size: 0.8em;
    }
    &__resultado {
        margin-top: 1rem;
        @include m.flex (row, nowrap, flex-end, center);
        & > button, & > a {
            font-size: 0.8rem;
        }
        & > button {
            padding: 0.5rem 1rem;
        }
        & > a {
            margin-right: 1rem;
        }
    }
    &__ver-cesta {
        color: blue;
        margin-left: 1rem;
        &:hover {
            text-decoration: underline;
        }
    }
}